@import url("../variable.css");

.card {
  width: 360px;
  height: 396px;
}

.wrap-card {
  margin-top: 40px;
}

.card-title {
  text-align: center;
  font-size: 18px;
  color: var(--color-logo);
  margin-top: 30px;
}

.img-main {
  margin-bottom: 32px;
}

.card-detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card-image {
  width: 64px;
  height: 59px;
}

.card-image__advantages {
  width: 100%;
  height: 100%;
}

.extesion {
  width: 230px;
  height: 24px;
  font-size: 16px;
  text-align: center;
}

.findMore {
  width: 198px;
  background-color: var(--color-logo);
  text-align: center;
  color: var(--nav-bg);
  border-radius: 50px;
  padding: 15px;
  margin-top: 20px;
}

.description-detail {
  width: 230px;
  height: 72px;
  text-align: center;
}


@media screen and (max-width: 768px) {
  .description-detail {
    width: 178px;
    height: 32px;
    font-size: 12px;
    text-align: center;
  }

  .findMore{
    width: 140px;
    padding: 7px;
    margin-top: 30px;
  }
}