.card {
  width: 360px;
  height: 396px;
}

.wrap-card {
  margin-top: 40px;
}
.card-title {
  text-align: center;
  font-size: 18px;
}
.card-price__detail {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.dolar {
  display: flex;
  align-items: center;
  font-size: 24px;
  line-height: 28.13px;
  margin-bottom: 8px;
}

.price {
  font-family: Roboto;
  font-size: 64px;
  font-weight: 400;
  line-height: 48px;
  letter-spacing: -0.0364em;
  text-align: center;
  margin: 18px 6px;
}
.year {
  text-align: center;
}

.personal-account {
  margin-top: 20px;
  font-size: 16px;
  text-align: center;
}

.account {
  padding: 2px 0;
}

.data {
  padding: 2px 0;
}

.upload-perday {
  padding: 2px 0;
}

.purchase {
  text-align: center;
  width: 198px;
  margin-top: 26px;
  margin-left: 78px;
  padding: 15px;
  border-radius: 50px;
}

@media screen and (max-width: 768px) {
  .card {
    width: 220px;
    height: 280px;
  }

  .card-title {
    font-size: 15px;
    margin-bottom: none;
  }

  .dolar {
    display: flex;
    align-items: center;
    font-size: 11px;
    line-height: 28.13px;
    margin-bottom: 8px;
  }

  .price {
    font-family: Roboto;
    font-size: 32px;
    font-weight: 400;
    line-height: 48px;
    letter-spacing: -0.0364em;
    text-align: center;
    margin: 10px 6px;
  }

  .purchase {
    width: 140px;
    margin-left: 36px;
    padding: 10px;
  }
}
